.tilmelding {
    padding: 32px;
    margin: auto;
    width: 90%;
    max-width: 450px;
    position: absolute;
    z-index: 8;
    top: 100px;
    left: 0;
    right: 0;
    transform: scale(0);
    opacity: 0;
    background: rgb(235, 235, 235);
    border-radius: 20%;
    transition: 350ms;

    .close-tilmeld {
        position: absolute;
        top: 10px;
        right: 14px;
        font-size: 120%;
        cursor: pointer;
    }

    h3 {
        text-align: center;
        margin: 0 0 16px 0;
        letter-spacing: 2px;
    }

    .tilmeld {
        display: grid;
        // width: 100%;
        margin: 0 auto;

        input[type=text],
        select {
            width: 100%;
            // display: block;
            padding: 8px 16px;
            // border-radius: 4px;
            border: 1px solid #ccc;
            border-radius: 4px;
            box-sizing: border-box;
            margin-bottom: 15px;
        }

        label {
            padding: 4px 8px;
            color: $tc-cool-grey;
        }

        input[type=submit] {
            width: 100%;
            background-color: tomato;
            color: white;
            padding: 14px 20px;
            margin: 24px 0 0 0;
            border: none;
            border-radius: 4px;
            cursor: pointer;
        }

        .dage {
            display: grid;
            // grid-template-columns: repeat( auto-fit, minmax(150px, 1fr) );
            grid-gap: 8px;

            label:not(:first-of-type) {
                // font-size: 90%;
                background: white;
                line-height: 100%;
                border: 1px solid #ccc;
                padding: 8px;
                border-radius: 4px;
                display: inline-block;

                span {
                    opacity: .4;
                }

                input[type=checkbox] {
                    margin-right: 8px;

                    &:checked+span {
                        opacity: 1;
                        color: $tc-ubuntu-orange;
                    }
                }
            }

            label:not(:first-of-type):hover {
                background: #f1f1f1;
                cursor: pointer;
            }
        }
    }
}

.toggle-tilmeld .tilmelding {
    transform: scale(1);
    border-radius: 4px;
    opacity: 1;
}